.header {
    margin-top: 60px;
}

.header-titles {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Lora', serif;
    font-style: italic;
    color: var(--text-color);

    .header-title-sm {
        position: absolute;
        top: 18%;
        font-size: 2rem;
    }

    .header-title-lg {
        position: absolute;
        top: 20%;
        font-size: 10rem;
    }
}

.header-img {
    width: 100%;
    height: 450px;
    margin-top: 80px;
    object-fit: cover;
}
